/**
 *  
 */
export enum CategoryTypes {
    EXPENSE,
    INCOME,
    MIXED
}

/**
 * 
 */
export class Category {
    /**
     *   
     */
    createdAt: Date;

    /**
     *  
     */
    description: string;

    /**
     * ID 
     */
    id: number;

    /**
     *  
     */
    title: string;

    /**
     *  
     */
    type: CategoryTypes;
}


