import {
    Component,
    OnInit
} from '@angular/core';

import {
    NavigationPaneItem
} from '../shared/components/navigation-pane/navigation-pane.component';

@Component({
    selector: 'm-reports',
    template: require('./reports.component.html'),
    styles: [require('./reports.component.less')]
})

export class ReportsComponent implements OnInit {
    items: NavigationPaneItem[];

    ngOnInit() {
        this.items = [
            {
                id: 'common',
                title: '',
                selected: true
            },
            {
                id: 'security',
                title: '',
            }
        ];
    }
}


