import {
    Component,
    Input
} from '@angular/core';

@Component({
    selector: 'm-button',
    template: require('./button.component.html'),
    styles: [require('./button.component.less')],
})

export class ButtonComponent {
    @Input() content: string;
    @Input() role: string;
}


